<template>
  <pre>{{convertedText}}</pre>
</template>

<script>
import resultTest from '@/assets/dataTest/csvjson'
export default {
  data () {
    return {
      convertedText: []
    }
  },
  mounted () {
    this.convert()
  },
  methods: {
    convert () {
      for (let i = 0; i < resultTest.length; i++) {
        const d = resultTest[i]
        this.convertedText.push(d.Close)
      }
    }
  }
}
</script>



